import React from 'react';
import PropTypes from 'prop-types';
import { createElementsFromText } from 'html-text-to-react';
import {Panel, PanelHeader, HeaderButton, platform, IOS, Div, View, Group, Form, Card, Root,InfoRow,List,Cell,Link,PullToRefresh,Button,Component,PanelHeaderEdit,ModalPage,ModalPageHeader,FormLayoutGroup,FormLayout,ModalRoot,Radio,SelectMimicry,Checkbox,Input} from '@vkontakte/vkui';
import persik from '../img/persik.png';
import './Persik.css';
import ReactDOM from 'react-dom'
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Icon24Cancel from '@vkontakte/icons/dist/24/cancel';
import Icon24Dismiss from '@vkontakte/icons/dist/24/dismiss';
import Icon24User from '@vkontakte/icons/dist/24/user';
const osname = platform();
const MODAL_PAGE_FILTERS = 'filters';
const MODAL_PAGE_COUNTRIES = 'countries';
const MODAL_PAGE_STORY_FEEDBACK = 'story-feedback';
const MODAL_PAGE_USER_INFO = 'user-info';

const MODAL_CARD_MONEY_SEND = 'money-send';
const MODAL_CARD_APP_TO_MENU = 'app-to-menu';
const MODAL_CARD_ABOUT = 'say-about';
const MODAL_CARD_NOTIFICATIONS = 'notifications';
const MODAL_CARD_CHAT_INVITE = 'chat-invite';
export default class Persik extends React.Component {

	constructor(props){
	super(props);

	this.state={
		title:"",
		text:"",
		mesto:"",
		stattime:"",
		owner_id:"",
		chatlink:"",
		type:"",
		igo:0,
		bookmark:0,
		}


	}

	get(url){
		var xhr = new XMLHttpRequest();
		xhr.open('POST', url, false);
		xhr.send();
		return xhr.responseText;
	}
	igo(){
		if (this.state.igo==1){
			this.setState({igo:0})
		}else{
			this.setState({igo:1})

		}
	}
	editbookmark(){
		if (this.state.bookmark==1){
			this.setState({bookmark:0})
		}else{
			this.setState({bookmark:1})

		}
	}
	componentDidMount() {
		const {title,text,go,mesto,starttime,type,owner_id}=this.props;
		var br = React.createElement("br")
		var f =text.split("\n").join("<br/>")





		this.setState({text:f})
	}

	buttons(){

			if(this.state.igo==1){
				return(
					<Div style={{display: 'flex'}}>
						<Button size="l" stretched style={{ marginRight: 8 }} level="outline" onClick={()=>this.igo()}>Не пойду</Button>
						<Button size="l" onClick={()=>this.editbookmark()} stretched level="secondary">{this.state.bookmark ==1? 'Убрать из закладок' : 'Добавить в закладки' }</Button>
					</Div>
				);
			}else if(this.state.igo==0){
				return(
					<Div style={{display: 'flex'}}>
						<Button size="l" stretched style={{ marginRight: 8 }} onClick={()=>this.igo()}>Я пойду</Button>
						<Button size="l" onClick={()=>this.editbookmark()} stretched level="secondary">{this.state.bookmark ==1? 'Убрать из закладок' : 'Добавить в закладки' }</Button>
					</Div>
				);
			}

	}

	getOwner(){
		const {title,text,go,mesto,starttime,type,owner_id}=this.props;




		var own="https://vk.com/id"+owner_id.toString();
		if(owner_id.toString()==="undefined"){
			return ""
		}else{
			return (<Cell>
				<InfoRow title="Организатор">
					<Link href={own}>vk.com/id{owner_id}</Link>
				</InfoRow>
			</Cell>);
		}
	}
	getChatlink(){
		const {title,text,go,mesto,starttime,type,owner_id,chatlink}=this.props;


		if(chatlink.toString()==="undefined"|| chatlink.toString()==="null" || chatlink.toString()===""){
			return ""
		}else{
			return (<Cell>
				<InfoRow title="Ссылка на чат">
					<Link href={chatlink}>{chatlink}</Link>
				</InfoRow>
			</Cell>);
		}
	}
	getGroup(){
		const {title,text,go,mesto,starttime,type,owner_id,grouplink}=this.props;


		if(grouplink.toString()==="undefined"|| grouplink.toString()==="null" || grouplink.toString()===""){
			return ""
		}else{
			return (<Cell>
				<InfoRow title="Группа">
					<Link href={grouplink}>{grouplink}</Link>
				</InfoRow>
			</Cell>);
		}
	}

	agreed_admin(){
		const {title,text,go,mesto,starttime,type,owner_id,agreed_admin}=this.props;


		if(agreed_admin.toString()==="undefined"|| agreed_admin.toString()==="null"){
			return (
				<Cell>
					<InfoRow title="Согласованность с администрацией города">
						Не требуется
					</InfoRow>
				</Cell>
			);
		}else{
			return (<Cell>
				<InfoRow title="Согласованность с администрацией города">
					{agreed_admin === "1"? "Согласовано" : "Не согласовано"}
				</InfoRow>
			</Cell>);
		}
	}

	getEditButton(){
		const {title,text,go,mesto,starttime,type,owner_id,can_edit}=this.props;
		if(can_edit==0){
			return(
				<Div style={{display: 'flex'}}>
					<Button size="l" stretched style={{ marginRight: 8 }} level="tertiary" onClick={()=>this.igo()}>Редактировать</Button>

				</Div>
			);
		}else{
			return "";
		}
	}


	render() {

		const {title,text,go,mesto,starttime,type,owner_id,can_edit,goroot}=this.props;
		const modal = (
			<ModalRoot activeModal="f">


				<ModalPage
					id="f"

					onClose={this.modalBack}
				>
					<List>
						<Cell>
							<InfoRow title="Дата рождения">
								30 января 1993
							</InfoRow>
						</Cell>
						<Cell>
							<InfoRow title="Родной город">
								Ереван
							</InfoRow>
						</Cell>
						<Cell>
							<InfoRow title="Место работы">
								Команда ВКонтакте
							</InfoRow>
						</Cell>
					</List>
				</ModalPage>


			</ModalRoot>
		);
		return (



				<View id="full" activePanel="full">
					<Panel id="full">

						<PanelHeader
							left={<HeaderButton onClick={goroot} data-to="glav">{osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}</HeaderButton>}>
							Событие
						</PanelHeader>
						<Group><Div>{title}</Div></Group>
						{this.buttons()}
						{this.getEditButton()}
						<Group>
							<Div>
								<InfoRow title="Примечание" id="prim">
									{createElementsFromText(this.state.text, {whitelistedHtmlTags: ['br'],})}
								</InfoRow>
							</Div>
						</Group>
						<Group>
							<List>
								<Cell>
									<InfoRow title="Место">
										{mesto}
									</InfoRow>
								</Cell>
								<Cell>
									<InfoRow title="Начало">
										{starttime}
									</InfoRow>
								</Cell>
								<Cell>
									<InfoRow title="Тип">
										{type}
									</InfoRow>
								</Cell>
								<ModalPage
									id="f"
									header={
										<ModalPageHeader

										>
											Выберите страну
										</ModalPageHeader>
									}

									settlingHeight={80}
								>
									<FormLayout>
										<Button level="secondary" size="xl">Информация о пользователе</Button>

										<FormLayoutGroup>
											<Button level="secondary" size="xl">Информация о пользователе</Button>

										</FormLayoutGroup>
									</FormLayout>
								</ModalPage>

								{this.getOwner()}
								{this.getChatlink()}
								{this.getGroup()}
								{this.agreed_admin()}
							</List>
						</Group></Panel>
				</View>



		);
	}
}
const style={
	fd: {

		width: '100%',
		display: 'flex',
		height: '100%',
		justifyContent: 'bottom',


	},
	buttons:{
		height: '100%',
		padding: '3px',
		width: '100%',


	},
};
Persik.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};


